import Axios from 'axios'
import axiosConfig from './config'
if (Axios.defaults.baseURL == undefined) {
    axiosConfig()
}
/**
 * @function 
 */
export const project = (params) => {
    return Axios({
        method: 'post',
        url: `/project`,
        data: params
    })
}
/**
 * @function 
 */
export const projectEdit = (params) => {
    return Axios({
        method: 'put',
        url: `/project`,
        data: params
    })
}

/**
 * @function ID
 * @param {id ID}
 */
export const getProject = (params) => {
    return Axios({
        method: 'get',
        url: `/projectOne/${params.id}`,
    })
}
/**
 * @function ID
 * @param {id ID}
 */
export const getProjectUser = (params) => {
    return Axios({
        method: 'get',
        url: `/projectUser/${params.id}`,
    })
}
/**
 * @function 
 * @param {name }
 * @param {type }
 * @param {status }
 * @param {pageNo }
 */
export const projects = (params) => {
    return Axios({
        method: 'get',
        url: `/projects?name=${params.name}&type=${params.type}&status=${params.status}&pageNo=${params.pageNo}`,
    })
}
