import Axios from 'axios'
import axiosConfig from './config'
if (Axios.defaults.baseURL == undefined) {
    axiosConfig()
}
/**
 * @function 
 */
export const getRealmTag = () => {
    return Axios({
        method: 'get',
        url: `/getRealmTag`
    })
}
/**
 * @function 
 */
export const collect = (params) => {
    return Axios({
        method: 'post',
        url: `/collect`,
        data: params
    })
}
/**
 * @function 
 */
export const cancelCollect = (params) => {
    return Axios({
        method: 'delete',
        url: `/collect`,
        data: params
    })
}

/**
 * @function 
 */
export const getCollect = (params) => {
    return Axios({
        method: 'get',
        url: `/collect?name=${params.name}&type=${params.type}&pageNo=${params.pageNo}&auType=${params.auType}`
    })
};

